.site-header {
	height: 60px;
	width: 100%;
	background-color: #673ab7;
	color: white;
	-webkit-box-shadow: 0px 2px 15px #333;
	        box-shadow: 0px 2px 15px #333;
}

.hamburger-icon {
	display: inline-block;
	padding: 0 1em;
	cursor: pointer;
}

.hamburger-icon:hover .bar {
	/*background-color: rgba(255,255,255,0.5);*/
}

.hamburger-icon.active > .bar:nth-child(1) {
	-webkit-transform-origin: 10% -18%;
	    -ms-transform-origin: 10% -18%;
	        transform-origin: 10% -18%;
	-webkit-transform: rotate(45deg);
	    -ms-transform: rotate(45deg);
	        transform: rotate(45deg);
}

.hamburger-icon.active > .bar:nth-child(2) {
	opacity: 0;
}

.hamburger-icon.active > .bar:nth-child(3) {
	-webkit-transform-origin: 0% 100%;
	    -ms-transform-origin: 0% 100%;
	        transform-origin: 0% 100%;
	-webkit-transform: rotate(-45deg);
	    -ms-transform: rotate(-45deg);
	        transform: rotate(-45deg);
}

.bar {
	height: 4px;
	width: 2em;
	background-color: #fff;
	margin-bottom: 5px;
	border-radius: 7px;
	-webkit-transition: all 0.4s;
	-o-transition: all 0.4s;
	transition: all 0.4s;
}
.bar:last-of-type {
	margin-bottom: 0;
}

.site-header .brand-name {
	margin: 0;
	line-height: 60px;
	display: inline-block;
	font-size: 30px;
}


